
body {
    font-family: 'Noto Sans HK', sans-serif;    
    padding: 0;
    margin: 0;
    background: url(../img/background_065.jpeg);
    background-size: cover;
    background-attachment: fixed;
}
.header {
    display: flex;
    position: relative;
    justify-content: space-between;
    padding: 30px 200px 30px 200px;
    align-items: center;
    font-size: 50px;
    background-color: rgba(255, 255, 255, 0.1);
}
.header-logo-link {
    color:white;
}
form {
    padding: 10px 30px;
    display: grid;
    grid-gap: 10px;
}
.modal-content {
    min-width: 400px;
    min-height: 200px;
    position: absolute;
    background-color: white;
    z-index: 1000;
    top:50%;
    left:50%;
    transform:translate(-50%, -50%);
    border-radius: 5px;
    border: 1px groove black;
}
.modal-header {
    text-align: center;
}
.btn-close {
    position: absolute;
    right: 2px;
    top: 5px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
}
.btn-close:hover {
    opacity: 1;
    }
    .btn-close:before, .btn-close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 15px;
    width: 2px;
    background-color: #333;
    }
    .btn-close:before {
    transform: rotate(45deg);
    }
    .btn-close:after {
    transform: rotate(-45deg);
    }
.login-form {
    display: grid;
    grid-gap: 10px;
    padding: 10px 30px;
}
.input-label {
    display: grid;
}
.login-form:last-child {
    margin-top: 10px;
}
.no-visits-notice {
    margin: 10% auto;
    color: white;
    font-size: 20px;
}
.main_container {
    padding: 30px 200px 30px 200px;
}
.visits {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
}
.visit-card {
    background-color: rgba(255, 255, 255, 0.1);
    margin: 5px;
    width: 300px;
    min-height: 200px;
    color: white;
}
.hidden {
    display: none;
}
.show-more-btn {
    margin-top: 20px;
}
.show-less-btn {
    margin-top: 20px;
    margin-bottom: 10px;
}
button {
    display: inline-block;
    color: black;
    text-decoration: none;
    user-select: none;
    padding: .5em 2em;
    outline: none;
    border: 2px solid;
    border-radius: 5px;
    transition: 0.2s;
    cursor: pointer;
}
button:hover { 
    background: rgba(255,255,255,.2);
    color: white;
}
button:active {
    background: white;
}
.filter-container {
    display: flex;
    justify-content: space-between;
    background-color: rgba(255, 255, 255, 0.1);
    color: white;
    max-width: 800px;
    margin: 15px auto;
    padding: 10px;
}
.overlay {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 20;
    transition: .3s all;
 }
 .overlay.active{
    opacity: 1;
    visibility: visible;
 }